.twemoji, .emojione {
  max-height: 1em;
  display: inline;
}

.ah-typeset img.emojione, .ah-typeset img.twemoji {
  margin-bottom: 0;
}

:root {
  --font-mono: Fira Mono, monospace;
}

.ah-typeset {
  color: var(--clr-text);
  font-family: Open Sans, sans-serif;
  font-size: 1.25rem;
}

@media (max-width: 800px) {
  .ah-typeset {
    margin-bottom: 1rem;
    font-size: 1rem;
  }
}

.ah-typeset h1, .ah-typeset h2, .ah-typeset h3, .ah-typeset h4, .ah-typeset h5, .ah-typeset h6 {
  color: var(--clr-title);
  margin-top: 1.5em;
  margin-bottom: .5em;
  font-family: Oswald, sans-serif;
  font-size: 1.25rem;
  line-height: 1.2;
}

@media (max-width: 800px) {
  .ah-typeset h1, .ah-typeset h2, .ah-typeset h3, .ah-typeset h4, .ah-typeset h5, .ah-typeset h6 {
    margin-bottom: 1rem;
  }
}

.ah-typeset h1 {
  font-size: 3.052rem;
}

@media (max-width: 800px) {
  .ah-typeset h1 {
    font-size: 1.953rem;
  }
}

.ah-typeset h2 {
  font-size: 2.441rem;
}

@media (max-width: 800px) {
  .ah-typeset h2 {
    font-size: 1.563rem;
  }
}

.ah-typeset h3 {
  font-size: 1.953rem;
}

@media (max-width: 800px) {
  .ah-typeset h3 {
    font-size: 1.25rem;
  }
}

.ah-typeset p, .ah-typeset img {
  margin-bottom: 1.5rem;
}

.ah-typeset p {
  color: var(--clr-text);
  text-align: justify;
  line-height: 2.2;
}

.ah-typeset a {
  color: var(--clr-text);
  word-break: break-word;
  background-image: linear-gradient(90deg, var(--clr-link), var(--clr-link));
  background-position: 0 100%;
  background-repeat: no-repeat;
  background-size: 100% 2px;
  text-decoration: none;
  transition: color .1s, background-size .1s;
}

.ah-typeset a:hover {
  color: var(--clr-link);
  background-size: 0 2px;
}

.ah-typeset img {
  margin-inline: auto;
}

.ah-typeset em {
  --clr-text: var(--clr-text-em);
  color: var(--clr-text);
  font-style: italic;
  font-weight: 700;
}

.ah-typeset strong {
  --clr-text: var(--clr-text-strong);
  color: var(--clr-text);
  font-weight: 900;
}

.ah-typeset li {
  color: var(--clr-text);
  margin-bottom: 1em;
  line-height: 2.2;
}

.ah-typeset li:before {
  color: var(--clr-bullet);
}

.ah-typeset input {
  border: 1px solid #0000;
  border-radius: .2em;
  padding: .2em .5em;
}

del {
  background: var(--clr-fail-primary-6);
  -webkit-text-decoration-color: hsl(var(--clr-fail-primary-8-hsl) / .5);
  text-decoration-color: hsl(var(--clr-fail-primary-8-hsl) / .5);
}

ins {
  background: var(--clr-success-primary-4);
  -webkit-text-decoration-color: var(--clr-success-primary-7);
  text-decoration-color: var(--clr-success-primary-7);
}

mark {
  background: var(--clr-warn-primary-5);
}

span.critic.comment {
  background: var(--clr-gray-primary-3);
}

.ah-content {
  max-width: 70ch;
  margin: 3em auto 2em;
}

@media (max-width: 800px) {
  .ah-content {
    padding: .5rem 1rem;
  }
}

.ah-content * {
  scroll-margin-top: var(--header-height, 50px);
}

